,#t3:hover,#t4:hover,#t5:hover,#t6:hover,#t7:hover,#t8:hover,#t9:hover,#t10:hover,#n0:hover,#n1:hover,#n2:hover
,#n3:hover,#n4:hover,#n5:hover,#n6:hover,#n7:hover,#n8:hover,#n9:hover,#n10:hover,#n11:hover,#n12:hover
,#n13:hover,#n14:hover,#n15:hover,#n16:hover,#n17:hover
{
  /* Start the shake animation and make the animation last for 0.5 seconds */
  animation: shake 0.5s;
  color: rgb(57, 138, 214);

  /* When the animation is finished, start again */
  animation-iteration-count: infinite;
}

@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}


.logo-holder {position:relative;text-align: center;/*dimensions of image*/}
.logo-holder .image,
.logo-holder .text {transition: opacity 0.5s ease-in-out;}
.logo-holder .text {position:absolute;text-align: center; opacity:0;}

.text{margin-top: -40%; margin-left: 10%;}
.logo-holder:hover .image {opacity:0;}
.logo-holder:hover .text {opacity:1; }
.logo-holder:hover .text{ font-size:"80%";color:"rgb(57, 138, 214)"}

